.partners{
  padding: 16px;
  &__container{
    justify-content: space-around;
    align-items: center;
    gap: 8px;
    @include tablet{
      flex-wrap: wrap;
      justify-content: center;
    }
  }
  &__logo{
    @include big-desktop{
      max-width: 140px;
    }
  }
  &__list{
    display: flex;
    align-items: center;
    gap: 12px;

    @include tablet{
      flex-wrap: wrap;
      justify-content: center;
    }
  }
  &__title{
    margin: 0;
    font-weight: 400;
    font-size: 11px;
    line-height: 16px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: rgba($color: #355362, $alpha: 0.6);
  }
  &__more{
    margin: 0;
    font-weight: 400;
    font-size: 11px;
    line-height: 16px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: rgba($color: #355362, $alpha: 0.6);
    transition: color 0.3 ease-in-out;
    &:focus{
      outline: none;
    }
    @include hover{
      color: #00A1FF;
    }
    &:active{
      color:hsl(4, 100%, 44%);
    }
  }
}
