.info{
  position: relative;
  background-color: #001414;
  height: 680px;
  display: flex;
  &::before{
    content: "";
    flex-shrink: 0;
    width: 287px;
    background-image: url(../img/info1.png);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;

    @include tablet{
      display: none;
    }
  }
  &::after{
    content: "";
    flex-shrink: 0;
    width: 287px;
    background-image: url(../img/info2.png);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    @include tablet{
      display: none;
    }
  }
  &__content{
    display: flex;
    flex-grow: 1;
    flex-direction: column;
    justify-content: center;
    flex-grow: 1;
    border-right: 1px solid #003331;
    border-left: 1px solid #003331;
    padding: 0 40px;
    height: 100%;
    @include tablet{
      padding: var(--offset);
    }
  }
  &__text{
    max-width: 542px;
  }
  &__title{
    margin: 0 auto;
    margin-bottom: 21px;
    font-weight: 400;
    font-size: 22px;
    line-height: 22px;
    color: #fff;
    text-align: left;
  }
  &__desck{
    margin: 0 auto;
    font-weight: 400;
    font-size: 15px;
    line-height: 24px;
    color: #fff;
  }
  @include tablet{
    height: auto;

  }
}
