.hero{
  display: flex;
  flex-direction: column;
  padding-top: 190px;
  padding-bottom: 40px;
  min-height: 776px;
  color: #fff;
  background-image: url(../img/hero-bg.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  &__container{
    margin-bottom: var(--offset);
  }

  &__content{
    grid-column: 6 span;

    @include small-tablet{
      grid-column: 4 span;
    }
  }
  &__list{
    @include small-tablet{
      row-gap: var(--offset);
    }
  }
  &__title{
    margin: 0;
    margin-bottom: 29px;
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 38px;
    line-height: 44px;
    letter-spacing: 1.2px;
    @include big-desktop{
      font-size: 34px;
    }
    @include small-tablet{
      font-size: 30px;
    letter-spacing: 0.5px;
    }
  }
  &__desc{
    margin: 0;
    margin-bottom: 29px;
    font-weight: 400;
    font-size: 18px;
    line-height: 28px;
    @include big-desktop{
      font-size: 17px;
    }
  }
  &__bototom-container{
    margin-top: auto;
    width: 100%;
  }
  &__subtitle{
    margin: 0;
    margin-bottom: 15px;
    font-weight: 400;
    font-size: 15px;
    line-height: 24px;
  }
  &__item{
    grid-column: 2 span;
  }
  &__link{
    display: block;
    border-top: 1px solid rgba($color: #ffffff, $alpha: 0.6);
    padding-top: 17px;
    font-weight: 400;
    font-size: 15px;
    line-height: 24px;
    color: #fff;
    transition: color 0.3s ease-in-out, border-color 0.3s ease-in-out;


    &:focus{
      outline: none;
    }
    @include hover{
      border-color: #00A1FF;
      color: #00A1FF;
    }
    &:active{
      border-color: hsl(4, 100%, 44%);
      color:hsl(4, 100%, 44%);
    }
  }
}















