.btn{
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border: 1px solid rgba($color: #ffffff, $alpha: 0.3);
  border-radius: 80px;
  padding: 17px 33px;
  font-size: 13px;
  line-height: 20px;
  letter-spacing: 0.02;
  color: #ffffff;
  transition: background-color 0.3s ease-in-out, border-color 0.3s ease-in-out;
  cursor: pointer;

  &:focus{
    outline: none;
  }
  @include hover{
    border-color: #00A1FF;
    background-color: #00A1FF;
  }
  &:active{
    border-color: hsl(4, 100%, 44%);
    background-color:hsl(4, 100%, 44%);
  }
}

