.header{
  padding: 17px 0;
  &__container{
    align-items: center;
    justify-content: space-between;

    @include small-tablet{
      flex-direction: row;
      justify-content: space-around;
    }
  }
  &--position{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
  }
}
.nav{
  display: flex;
  align-items: center;
  padding-right: 16px;

  &__list{
    align-items: center;
    margin-right: 32px;
    gap: 51px;

    @include small-tablet{
      flex-direction: column;
      align-items: flex-start;
      margin-right: 0;
    }
  }
  &__link{
    font-size: 13px;
    color: #fff;
    transition: color 0.3s ease-in-out;

    &:focus{
      outline: none;
    }
    @include hover{
      color: #00A1FF;
    }
    &:active{
      color: #E00E00;
    }
    @include small-tablet{
      font-size: 16px;
    }
   }
   @include small-tablet{
    position: fixed;
    left: 0;
    top: 0;
    padding: var(--offset);
    width: 60%;
    background-color: #001414;
    height: 100vh;
    z-index: 100;
    overflow-y: auto;
    visibility: hidden;
    transform: translateX(-100%);
    transition: transform 0.3s ease-in-out, visibility 0.3s ease-in-out;
    // display: none;
  }
  @include small-tablet{
    flex-direction: column;
    align-items: flex-start;
    gap: 51px;
  }
}

.nav__link.focus-visible{
  outline: 1px solid #E00E00;
  outline-offset: 3px;
}
.nav.menu--active{
  transform: none;
  visibility: visible;
}
