.approach{
  &__top{
    padding: 80px 0;
    background-color: #F7F8F8;
  }
  &__left{
    grid-column: 5 span;
    padding-top: 33px;

    @include big-desktop{
      margin-bottom: var(--offset);
    }
    @include small-tablet{
      grid-column: 4 span;
    }
  }
  &__right{
    display: flex;
    align-items: flex-end;
    grid-column: 7 / 6 span;
    gap: 8px;

    @include big-desktop{
      grid-column: 12 span;
      justify-content: flex-start;
    }
    @include small-tablet{
      grid-column: 4 span;
      flex-wrap: wrap;
    }
  }
  &__title{
    margin: 0;
    margin-bottom: 13px;
    font-weight: 400;
    font-size: 22px;
    line-height: 22px;
    color: #003331;
  }
  &__desck{
    margin: 0;
    margin-bottom: 37px;
    font-weight: 400;
    font-size: 15px;
    line-height: 24px;
    color: rgba($color: #355362, $alpha: 0.8);
  }
  &__bottom{
    background-color: #fff;
    padding: 24px 0;
  }
  &__btn{
    padding: 16px 34px;
    color: #00413E;
    border: 1px solid rgba($color: #355362, $alpha: 0.2);
  }
  &__item{
    grid-column: 2 span;
  }
  &__link{
    display: block;
    border-top: 1px solid rgba($color: #355362, $alpha: 0.3);
    padding-top: 16px;
    padding-left: 32px;
    font-weight: 400;
    font-size: 15px;
    line-height: 24px;
    color: rgba($color: #355362, $alpha: 0.8);
    background-position: left top 15px;
    background-size: 24px 24px;
    background-repeat: no-repeat;
    transition: color 0.3s ease-in-out, border-color 0.3s ease-in-out;
    @include small-tablet{
      font-size: 14px;
      line-height: 20px;
    }
    &:focus{
      outline: none;
    }
    @include hover{
      border-color: #00A1FF;
      color: #00A1FF;
    }
    &:active{
      border-color: hsl(4, 100%, 44%);
      color:hsl(4, 100%, 44%);
    }
    &--ted{
      background-image: url(../img/icons-approach1.png);
    }
    &--nyt{
      background-image: url(../img/icons-approach2.png);
    }
    &--scoop{
      background-image: url(../img/icons-approach3.png);
    }
    &--press{
      background-image: url(../img/icons-approach4.png);
    }
    &--time{
      background-image: url(../img/icons-approach5.png);
    }
    &--free{
      background-image: url(../img/icons-approach6.png);
    }
  }
}
