.where{
  padding-top: 155px;
  padding-bottom: 133px;
  background-color: #001F1F;
  color: #fff;
  @include small-tablet{
    padding-top: 135px;
    padding-bottom: 103px;
  }
  &__content{
    grid-column: 4 span;
    @include small-tablet{
      margin-bottom: 20px;
    }
  }
  &__map{
    grid-column: 7 / 5 span;
    @include small-tablet{
      grid-column: 4 span;
    }
    &-image{
      @include small-tablet{
        max-width: 100%;
      }
    }
  }
  &__title{
    margin: 0;
    font-family: "LibreBaskerville";
    font-weight: 400;
    font-size: 36px;
    line-height: 144%;
    letter-spacing: 0.03em;
    color: #fafafa;
    margin-bottom: 43px;
  }
  &__descr{
    margin: 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 156%;
    color: #fafafa;
    margin-bottom: 37px;
  }
  &__btn{
    border: none;
    border-radius: 80px;
    background: #00665f;
    font-weight: 400;
    font-size: 14px;
    line-height: 143%;
    letter-spacing: 0em;
    text-align: center;
    color: #fff;
  }
}
