.footer{
  &__top{
    background-color: #001414;
    padding-bottom: 137px;

    .container{
      @include small-tablet{
        row-gap: 32px;
      }
    }
  }
  &__column{
    grid-column: 2 span;

    &:last-child{
      grid-column: 11 / 2 span;
      @include small-tablet{
        grid-column: 2 span;
      }
    }
  }
  &__title{
    position: relative;
    margin: 0;
    margin-top: 112px;
    font-weight: 400;
    font-size: 11px;
    line-height: 145%;
    letter-spacing: 0.09em;
    text-transform: uppercase;
    color: #7d9798;
    padding-bottom: 24px;
    margin-bottom: 23px;
    &::after{
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 16px;
      height: 1px;
      background: #25b895;
    }
  }
  &__link{
    font-weight: 400;
    font-size: 15px;
    line-height: 200%;
    color: #edeceb;
  }
  &__bottom{
    padding-bottom: 63px;
    padding-top: 40px;
    background: #000f0f;
    &-container{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }
  &__sublist{
    max-width: 270px;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 8px;

  }
  &__sublist-link{
    margin: 0;
    font-weight: 400;
    font-size: 10px;
    line-height: 100%;
    color: #e5ecec;
  }
}
