.information{
  background-color: #f4f5f5;
  padding-bottom: 140px;
  @include small-tablet{
    padding-bottom: 80px;
  }
  &__text{
    grid-column: 6 span;
    @include small-tablet{
      grid-column: 4 span;
    }
  }
  &__title{
    margin: 0;
    max-width: 80%;
    font-family: 'LibreBaskerville';
    font-weight: 400;
    font-size: 31px;
    line-height: 123%;
    letter-spacing: 0.04em;
    color: #003331;
    @include small-tablet{
      max-width: 100%;
      margin-bottom: 25px;
      font-size: 24px;
    }
  }
  &__list{
    display: grid;
    grid-column: 7 / 6 span;
    grid-template-columns: repeat(6, 1fr);
    column-gap: var(--offset);
    row-gap: 36px;
    @include small-tablet{
      grid-template-columns: repeat(4, 1fr);
      grid-column: 4 span;
    }
  }
  &__item{
    grid-column: 3 span;
    @include small-tablet{
      grid-column: 4 span;
    }
  }
  &__subtitle{
    margin: 0;
    margin-bottom: 4px;
    font-weight: 400;
    font-size: 15px;
    line-height: 160%;
    color: #003331;
  }
  &__descr{
    font-weight: 400;
    font-size: 15px;
    line-height: 160%;
    color: rgba(53, 83, 98, 0.8);
    }
}
