.steps{
  padding: 120px 0;
  background-color: #fff;
  @include small-tablet{
    padding: 60px 0;
  }
  &__title{
    margin: 0 auto;
    margin-bottom: 57px;
    max-width: 720px;
    font-family: var(--second-family);
    font-size: 30px;
    font-weight: 400;
    line-height: 38px;
    letter-spacing: 1.2px;
    text-align: center;
    color: #003331;

    @include small-tablet{
      font-size: 24px;
      line-height: 32px;
    }
  }
  &__list{
    counter-reset: section;
    justify-content: space-between;
    margin: 0 auto;
    max-width: 758px;
    @include small-tablet{
      flex-wrap: wrap;
    }
  }
  &__item{
    display: flex;
    flex-direction: column;
    align-items: center;
    &::before{
      counter-increment: section;
      content: counter(section);
      display: flex;
      align-items: center;
      justify-content: center;
      margin-bottom: 28px;
      width: 40px;
      height: 40px;
      background: #014C48;
      border-radius: 100%;
      color: #fff;
    }
    @include small-tablet{
      width: 100%;
      &::after{
        display: none;
      }
      &::before{
        margin-bottom: 15px;
      }
    }
    &--line{
      position: relative;
      flex-grow: 1;
      &::before{
        display: none;
      }
      &::after{
        content: "";
        position: absolute;
        left: 0;
        top: 50%;
        width: 100%;
        height: 0.5px;
        margin-top: -35px;
        transform: translateY(-50%);
        background-color: #35536241;
      }
    }
  }
  &__descr{
    margin: 0;
    max-width: 142px;
    font-weight: 400;
    font-size: 15px;
    line-height: 24px;
    text-align: center;
    color: #355362;
    @include small-tablet{
      margin-bottom: 20px;
    }
  }
}
