.press{
  padding-top: 87px;
  padding-bottom: 87px;
  background-color: #fff;

  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 33px;


    @include small-tablet{
      flex-wrap: wrap;
      gap: 20px;
    }

  }
  &__title{
    margin: 0;
    font-family: 'LibreBaskerville';
    font-weight: 400;
    font-size: 27px;
    line-height: 141%;
    letter-spacing: 0.04em;
    color: #003331;
  }
  &__link{
    font-weight: 400;
    font-size: 13px;
    line-height: 154%;
    letter-spacing: 0em;
    text-align: center;
    color: #00413e;
    border: 1px solid rgba(53, 83, 98, 0.2);
    border-radius: 80px;
  }
  &__item{
    grid-column: 4 span;
  }
  &__list{
    row-gap: var(--offset);
  }
}
.press-card{
  position: relative;
  min-height: 145px;
  padding-top: 16px;
  border-top: 1px solid rgba(53, 83, 98, 0.3);
  &__top{
    display: flex;
    align-items: center;
    gap: 16px;

  }
  &__date{
    font-weight: 400;
    font-size: 11px;
    line-height: 145%;
    letter-spacing: 0.05em;
    color: rgba(53, 83, 98, 0.6);
  }
  &__author{
    margin: -18px 0;
    font-weight: 400;
    font-size: 11px;
    line-height: 145%;
    letter-spacing: 0.05em;
    padding: 18px 0;
    text-transform: uppercase;
    color: rgba(53, 83, 98, 0.6);
    padding-left: 32px;
    background-position: left top 15px;
    background-size: 24px 24px;
    background-repeat: no-repeat;
    transition: color 0.3s ease-in-out, border-color 0.3s ease-in-out;

    &--ted{
      background-image: url(../img/icons-approach1.png);
    }
    &--cor{
      background-image: url(../img/press1.png);
    }
    &--dep{
      background-image: url(../img/press2.png);
    }
    &--new{
      background-image: url(../img/icons-approach2.png);
    }
    &--cor2{
      background-image: url(../img/press3.png);
    }
    &--roc{
      background-image: url(../img/press4.png);
    }
  }
  &___title{
    position: relative;
    font-weight: 400;
    font-size: 17px;
    line-height: 145%;
    color: #00413e;
  }
  &__link{
    color: #00413e;

    &::after{
      content: "";
      position: absolute;
      inset: 0;
    }
  }
}
